import { GLShaderCommon } from '../Utility/GLShaderCommon';
import { AttribLocations } from './AttribLocations';
import { Modal } from './Modal';

export class UniformLoc {
    uPointSize: WebGLUniformLocation | null;
    uAngle: WebGLUniformLocation | null;
}

export class Shader {

    gl: WebGL2RenderingContext;
    program: WebGLProgram | null;

    attribLoc: AttribLocations;
    uniformLoc: UniformLoc;

    constructor(gl: WebGL2RenderingContext, vertShaderSrc: string, fragShaderSrc: string) {
        this.program = GLShaderCommon.getShaderProgram(gl, vertShaderSrc, fragShaderSrc, true);

        if (this.program != null) {
            this.gl = gl;
            gl.useProgram(this.program);
            this.attribLoc = new AttribLocations(gl, this.program);
            this.uniformLoc = new UniformLoc();
            // this.uniformLoc = {};	// TODO : Replace in later lessons with get standardUniformLocations.
        }

        // Note :: Extended shaders should deactivate shader when done calling super and setting up custom parts in the constructor.
    }

    // ...................................................
    // Methods
    activate() {
        this.gl.useProgram(this.program); return this;
    }
    deactivate() {
        this.gl.useProgram(null); return this;
    }

    // function helps clean up resources when shader is no longer needed.
    dispose() {
        // unbind the program if its currently active
        if (this.gl.getParameter(this.gl.CURRENT_PROGRAM) === this.program) { this.gl.useProgram(null); }
        this.gl.deleteProgram(this.program);
    }

    // ...................................................
    // RENDER RELATED METHODS

    // Setup custom properties
    preRender() {
        //
    }
    // Handle rendering a modal
    renderModal(modal: Modal) {
        this.gl.bindVertexArray(modal.mesh.vao);	// Enable VAO, this will set all the predefined attributes for the shader

        if (modal.mesh.indexCount) {
            this.gl.drawElements(modal.mesh.drawMode, modal.mesh.indexCount, this.gl.UNSIGNED_SHORT, 0);
        } else {
            this.gl.drawArrays(modal.mesh.drawMode, 0, modal.mesh.vertexCount);
        }

        this.gl.bindVertexArray(null);

        return this;
    }
}