
import { RTN } from '../../WebGL/VAO';
import { Modal } from '../../WebGL/Modal';
import { Shader } from '../../WebGL/Shader';

import { NGL3dApp } from '../..';
import { GeometryBase } from '../Core/Base';
import { ATTR } from '../../WebGL/AttribLocations';
// import { MeshCommon } from '../../WebGL/MeshCommon';

class GridAxisShader extends Shader {

    constructor(gl: WebGL2RenderingContext, vertSrc: string, fragSrc: string, aryColor: number[]) {
        super(gl, vertSrc, fragSrc);	// Call the base class constructor which will setup most of what we need

        // Our shader uses custom uniforms, this is the time to get its location for future use.
        if (this.program) {
            // this.uniformLoc.uPointSize = gl.getUniformLocation(this.program, 'uPointSize');
            this.uniformLoc.uAngle = gl.getUniformLocation(this.program, 'uAngle');
            var uColor = gl.getUniformLocation(this.program, 'uColor');
            gl.uniform3fv(uColor, aryColor);
        }
        gl.useProgram(null); // Done setting up shader
    }

    // Simple function that passes in Angle and Pointsize uniform data to the shader program.
    set(angle: number) {
        this.gl.uniform1f(this.uniformLoc.uAngle, angle);
        return this;
    }
    renderModal(modal: Modal) {
        this.gl.bindVertexArray(modal.mesh.vao);	// Enable VAO, this will set all the predefined attributes for the shader

        if (modal.mesh.indexCount) {
            this.gl.drawElements(modal.mesh.drawMode, modal.mesh.indexCount, this.gl.UNSIGNED_SHORT, 0);
        } else { this.gl.drawArrays(modal.mesh.drawMode, 0, modal.mesh.vertexCount); }

        this.gl.bindVertexArray(null);

        return this;
    }
}
export class GridAxis extends GeometryBase {
    v = `#version 300 es
    in vec3 a_position;	//Standard position data.
    layout(location=4) in float a_color;	//Will hold the 4th custom position of the custom position buffer.

    uniform vec3 uColor[4];	//Color Array
    uniform float uAngle;

    out lowp vec4 color;	//Color to send to fragment shader.

    void main(void) {
        color = vec4(uColor[ int(a_color) ],1.0); //Using the 4th float as a color index.
        // gl_Position = vec4(a_position, 1.0);
        gl_Position = vec4(cos(uAngle) * 0.3 + a_position.x, sin(uAngle) * 0.3 + a_position.y, a_position.z, 1.0);
    }
    `;
    f = `#version 300 es
    precision mediump float;

    in vec4 color;
    out vec4 finalColor;

    void main(void) {
        finalColor = color;
        // finalColor = vec4(1, 0, 0, 1);
    }
    `;
    app: NGL3dApp;
    gl: WebGL2RenderingContext;

    mesh: RTN;
    modal: Modal;
    shader: GridAxisShader;

    ro: number = 0;
    constructor(app: NGL3dApp) {
        super(app.scene);
        this.app = app;
        this.gl = app.canvas.gl;
        this.initShader(this.gl);
    }
    GetVAO() {
        var verts = [],
            size = 1.8,			// W/H of the outer box of the grid, from origin we can only go 1 unit in each direction, so from left to right is 2 units max
            div = 10.0,			// How to divide up the grid
            step = size / div,	// Steps between each line, just a number we increment by for each line in the grid.
            half = size / 2;	// From origin the starting position is half the size.

        var p;	// Temp variable for position value.
        for (let i = 0; i <= div; i++) {
            // Vertical line
            p = -half + (i * step);
            verts.push(p);		// x1
            verts.push(half);	// y1
            verts.push(0);		// z1
            verts.push(0);		// c2

            verts.push(p);		// x2
            verts.push(-half);	// y2
            verts.push(0);		// z2
            verts.push(1);		// c2

            // Horizontal line
            p = half - (i * step);
            verts.push(-half);	// x1
            verts.push(p);		// y1
            verts.push(0);		// z1
            verts.push(0);		// c1

            verts.push(half);	// x2
            verts.push(p);		// y2
            verts.push(0);		// z2
            verts.push(1);		// c2
        }

        // TODO : Remove the following, its only to demo extra lines can be thrown in.
        verts.push(-half);	// x1
        verts.push(-half);	// y1
        verts.push(0);		// z1
        verts.push(2);		// c2

        verts.push(half);	// x2
        verts.push(half);	// y2
        verts.push(0);		// z2
        verts.push(2);		// c2

        verts.push(-half);	// x1
        verts.push(half);	// y1
        verts.push(0);		// z1
        verts.push(3);		// c2

        verts.push(half);	// x2
        verts.push(-half);	// y2
        verts.push(0);		// z2
        verts.push(3);		// c2

        // Setup
        // let attrColorLoc = 4;
        this.mesh = new RTN(); // { drawMode:gl.LINES, vao:gl.createVertexArray() };
        this.mesh.drawMode = this.gl.LINES;
        this.mesh.vao = this.gl.createVertexArray();

        // Do some math
        this.mesh.vertexComponentLen = 4;
        this.mesh.vertexCount = verts.length / this.mesh.vertexComponentLen;
        let strideLen = Float32Array.BYTES_PER_ELEMENT * this.mesh.vertexComponentLen; // Stride Length is the Vertex Size for the buffer in Bytes

        // Setup our Buffer
        this.mesh.bufVertices = this.gl.createBuffer();
        this.gl.bindVertexArray(this.mesh.vao);
        this.gl.bindBuffer(this.gl.ARRAY_BUFFER, this.mesh.bufVertices);
        this.gl.bufferData(this.gl.ARRAY_BUFFER, new Float32Array(verts), this.gl.STATIC_DRAW);
        this.gl.enableVertexAttribArray(ATTR.ATTR_POSITION_LOC);
        this.gl.enableVertexAttribArray(ATTR.ATTR_COLOR_LOC);

        this.gl.vertexAttribPointer(
            ATTR.ATTR_POSITION_LOC						// Attribute Location
            , 3										// How big is the vector by number count
            , this.gl.FLOAT 								// What type of number we passing in
            , false									// Does it need to be normalized?
            , strideLen								// How big is a vertex chunk of data.
            , 0										// Offset by how much
        );

        this.gl.vertexAttribPointer(
            ATTR.ATTR_COLOR_LOC							// new shader has "in float a_color" as the second attrib
            , 1										// This atttrib is just a single float
            , this.gl.FLOAT
            , false
            , strideLen								// Each vertex chunk is 4 floats long
            , Float32Array.BYTES_PER_ELEMENT * 3		// skip first 3 floats in our vertex chunk, its like str.substr(3,1) in theory.
        );

        // Cleanup and Finalize
        this.gl.bindVertexArray(null);
        this.gl.bindBuffer(this.gl.ARRAY_BUFFER, null);
        // this.gl.mMeshCache["grid"] = this.mesh;
        return this.mesh;
    }
    initShader(gl: WebGL2RenderingContext) {
        const col = [0.8, 0.8, 0.8, 1, 0, 0, 0, 1, 0, 0, 0, 1];
        this.shader = new GridAxisShader(gl, this.v, this.f, col);
        this.mesh = this.GetVAO();
        this.modal = new Modal(this.mesh);
    }
    render(gl: WebGL2RenderingContext) {
        this.shader.activate();
        this.ro += 0.04;
        this.shader.set(this.ro);
        this.shader.renderModal(this.modal);
    }
}