export enum ATTR {
    ATTR_POSITION_NAME = 'a_position',
    ATTR_POSITION_LOC = 0,
    ATTR_NORMAL_NAME = 'a_norm',
    ATTR_NORMAL_LOC = 1,
    ATTR_UV_NAME = 'a_uv',
    ATTR_UV_LOC = 2,

    ATTR_COLOR_LOC = 4,
    ATTR_COLOR_NAME = 'a_color',
}

export class AttribLocations {
    position: number;
    normal: number;
    uv: number;
    constructor(gl: WebGL2RenderingContext, program: WebGLProgram) {
        this.position = gl.getAttribLocation(program, ATTR.ATTR_NORMAL_NAME);
        this.normal = gl.getAttribLocation(program, ATTR.ATTR_NORMAL_NAME);
        this.uv = gl.getAttribLocation(program, ATTR.ATTR_UV_NAME);
    }
}